@use '../../utils/variables' as *;
@use '../../utils/mixins' as *;

.product-details {
  display: flex;
  flex-direction: column;
  gap: 16px;

  padding-top: 24px;
  overflow: hidden;

  // overflow-wrap: break-word;

  &__top {
    grid-column: 1 / 5;
  }
}

.breadcrumbs {
  @include grid;

  grid-template-rows: auto auto;
  row-gap: 25px;

  &__wrapper {
    grid-column: 1 / 5;

    display: flex;
    align-items: flex-end;
    gap: 6px;
    height: 16px;
  }

  &__next {
    width: 5.33px;
    height: 9.33px;

    padding: 3.33px 5.33px;
  }

  &__category {
    height: 15px;

    font-family: $font-family;
    font-size: 12px;
    font-weight: 600;
    line-height: 16px;

    text-decoration: none;
    color: $color-primary;
  }

  &__product-name {
    font-family: $font-family;
    font-size: 12px;
    font-weight: 700;
    line-height: 15px;
    text-align: left;
    color: $color-secondary;
    overflow-wrap: break-word;
    width: 162px;
    align-self: center;
  }

  .back-link {
    grid-column: 1 / 2;
    justify-self: start;

    display: flex;
    align-items: flex-end;
    justify-content: flex-start;
    gap: 4px;

    padding: 0;
    height: 16px;

    background-color: transparent;
    border: none;

    &__image-wrapper {
      height: 16px;
      width: 16px;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    &__text {
      height: 15px;
      font-family: $font-family;
      font-size: 12px;
      font-weight: 700;
      line-height: 15.34px;
      text-align: left;

      color: $color-secondary;
    }
  }
}

.product-content {
  @include grid;

  grid-template-rows: auto;
  row-gap: 32px;

  &__name {
    grid-column: 1 / 5;
  }

  &__images {
    grid-column: 1 / 5;
  }
  &__options {
    grid-column: 1 / 5;
  }
  &__purchase {
    grid-column: 1 / 5;
  }
  &__main-info {
    grid-column: 1 / 5;
  }
}

.product-images {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;

  &__main-image {
    width: 288px;
    height: 288px;
    object-fit: contain;
  }

  // &__images-list {
  // }
}

.images-list {
  display: flex;
  align-self: center;
  gap: 8px;

  // &__element {
  // }
  &__image {
    box-sizing: border-box;
    width: 49px;
    height: 49px;
    padding: 4.5px;

    border: 1px solid $color-elements;
    object-fit: contain;

    &--active {
      border-color: $color-primary;
    }
  }
}

.options {
  display: flex;
  flex-direction: column;
  gap: 24px;

  // &__colors {
  // }
  &__divider {
    width: 100%;
    height: 1px;
    background-color: $color-elements;
    border: none;

    margin: 0;
  }

  // &__capacities {
  // }
}

.colors {
  display: flex;
  flex-direction: column;
  gap: 8px;

  &__text-wrapper {
    display: flex;
    justify-content: space-between;
  }

  &__available {
    color: $color-secondary;
  }
  &__id {
    color: $color-icons-placeholders;
  }

  // &__colors-list {
  // }
}

.purchase {
  display: flex;
  flex-direction: column;
  gap: 16px;

  // &__prices {
  // }
  // &__add-buttons {
  // }
}

.prices {
  display: flex;
  align-items: center;
  gap: 8px;

  // &__regular {

  // }
  &__discount {
    font-family: $font-family;
    font-size: 22px;
    font-weight: 500;
    line-height: 28.12px;
    text-align: left;
    color: $color-secondary;
    text-decoration: line-through;
  }
}

.add-buttons {
  display: flex;
  gap: 8px;

  &__cart {
    background-color: $color-primary;
    color: $color-white;
    height: 48px;

    border: none;
    flex-grow: 1;
  }

  // &__wishlist {
